
* {
  padding: 0;
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  max-width: 600px;
  margin: 60px auto 0 auto;
}
#title {
  text-align: center;
  text-decoration: underline;
}
.container {
  margin: 0 100px;
}
.inputText {
  width: 100%;
  height: 30px;
  border: none;
  border-bottom: 1px solid #ddd;
  font-size: 20px;
  margin: 15px auto;
}
ul {
  list-style: none;
}
li {
  margin-bottom: 12px;
  border-bottom: 1px solid #d2d2d2;
}
.delete {
  float: right;
  width: 12%;
  height: 16px;
  background: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.delete:hover, .control .clear:hover {
  background: #FF6A6A;
  color: #fff;
}
.active {
  text-decoration: line-through;
}
.control .clear {
  float: right;
  width: 20%;
  height: 30px;
  background: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}





